.menu-bars {
    color: white;
    font-size: 2.5rem;
    margin: 0.2rem;
}

.colored-menu {
    display: flex;
    justify-content: flex-end;
    width: 70%;
    height: 4rem;
    margin: 0;
    right: 0;
    top: 0;
    position: absolute;
    padding-right: 1rem;
    padding-top: 1rem;
}

@media screen and (min-width: 1200px) {
    .menu-bars,
    .colored-menu {
        display: none;
    }
}